ntainer.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst historyDatatableColumns = ref(['jobID', 'targetObject', 'type', 'createDate', 'createdBy', 'status']);\nconst curStepJob = ref(undefined as unknown as IExportJobItem | undefined);\nconst curArtifactJob = ref(undefined as unknown as IExportJobItem | undefined);\nconst localization = container.resolve<ILocalization>(ILocalization);\ninterface IExportJobListProps {\n    jobType: JobType;\n}\n\nconst props = defineProps<IExportJobListProps>();\n\nonUnmounted(() => {\n    viewModel.exportJobs = [];\n});\n\nconst optionsDataTable = ref({\n    filterable: false, // omit the `id` column,\n    perPage: 5,\n    perPageValues: [],\n    sortIcon: {\n        base: 'mdi',\n        is: 'mdi mdi-unfold-less-horizontal',\n        up: 'mdi mdi-arrow-up',\n        down: 'mdi mdi-arrow-down'\n    },\n    headings: {\n        jobID: localization.get(\"jobID\"),\n        targetObject: `${localization.get(\"application\")}/${localization.get(\"module\")}`,\n        type: localization.get(\"type\"),\n        createDate: localization.get(\"createDate\"),\n        createdBy: localization.get(\"createdBy\"),\n        status: localization.get(\"status\"),\n    },\n    showChildRowToggler: true,\n    childRowTogglerFirst: false,\n    uniqueKey: \"jobID\",\n    texts: {\n        count: '',\n    },\n\n});\n\ninterface IAppValues {\n    jobID: string;\n    targetObject: string;\n    type: string;\n    createDate: string;\n    createdBy: string;\n    status: string;\n\n\n};\n\nconst jobValues = computed(() => {\n    return viewModel.exportJobs!.map<IAppValues>(job => {\n        const name = props.jobType == \"deploy\" ? (job.jobData as IDeployJobData).deployObject.name : (job.jobData as IExportJobData).app.name\n        const retVal: IAppValues = {\n            jobID: job.jobID,\n            targetObject: name, //job.jobData.app?.name,\n            type: job.jobData.type,\n            createDate: job.createDate,\n            createdBy: job.createdBy,\n            status: job.status,\n\n        };\n        return retVal;\n    });\n});\n\nexecutor.execute(async () => {\n    await executor.executeUseCase<IListExportJobs>(IListExportJobs, {jobType: props.jobType});\n}, { loading: true });\n\n\nfunction computedStatusIcon(status: string) {\n    if (status == 'failed') {\n        return 'mdi mdi-close-circle-outline failedIcon'\n    }\n    if (status == 'success') {\n        return 'mdi mdi-check-circle-outline successIcon'\n    }\n    if (status == 'waiting') {\n        return 'mdi mdi-timer-sand waitingIcon'\n    }\n    if (status == 'running') {\n        return 'mdi mdi-progress-upload runningIcon'\n    }\n}\n\n</script>\n\n<template>\n    <div>\n        <div>\n            <v-client-table v-if=\"jobValues.length > 0\" :data=\"jobValues\" :columns=\"historyDatatableColumns\"\n                :options=\"optionsDataTable\">\n                <template #status=\"props\">\n                    <div style=\"display:flex\">\n                        <i :class=\"computedStatusIcon(props.row.status)\"></i>\n                        <a style='padding-left:5px'> {{ props.row.status }}</a>\n                    </div>\n                </template>\n\n                <template v-slot:child_row=\"props\">\n                    <div v-if=\"props.row.status != 'waiting'\" data-bs-toggle=\"collapse\" href=\"#multiCollapseExample1\"\n                        role=\"button\" aria-expanded=\"false\" aria-controls=\"multiCollapseExample1\">\n                        <ExportJobSteps :job=\"props.row\" />\n                    </div>\n                </template>\n            </v-client-table>\n            <div v-if=\"jobValues.length == 0\">This model has no job</div>\n        </div>\n    </div>\n</template>\n\n<style scoped>\ntable {\n    font-family: arial, sans-serif;\n    border-collapse: collapse;\n    width: 100%;\n}\n\ntd,\nth {\n    border: 1px solid #dddddd;\n    text-align: left;\n    padding: 8px;\n}\n\ntr:nth-child(even) {\n    background-color: #dddddd;\n}\n\n.div-box {\n    margin-left: 10px;\n    margin-top: 5px;\n    width: 50%;\n    border-style: dotted;\n}\n\n.failedIcon {\n    color: red;\n    font-size: 16px\n}\n\n.successIcon {\n    color: #149911;\n    font-size: 16px\n}\n\n.waitingIcon {\n    color: #A1A1A1;\n    font-size: 16px\n}\n\n.runningIcon {\n    color: #14ADFC;\n    font-size: 16px\n}\n.artifactIcons{\n    cursor: pointer;\n}\n</style>\n\n"],"sourceRoot":""}]);
